<template>
  <!--    <img :src='QRUrl' class="img-circle" width="35px" style="" @click="downloadIamgeMUI"/>-->

  <van-dialog v-model="show" show-cancel-button class="service-box" cancelButtonText="取消" confirmButtonText="保存二维码" cancelButtonColor="#1988FF" confirmButtonColor="#fff" @cancel="cancelService" @confirm="downloadIamgeMUI">
    <div slot="default" class="dialog-content" :style="{backgroundImage: 'url('+imageUrl+')'}">
      <img :src="QRUrl" class="dialog-img"/>
      <div class="dialog-tips">请使用微信扫描二维码，联系客服</div>
      <div class="dialog-but">
      </div>
    </div>
  </van-dialog>
</template>

<script>
  export default {
    name: 'BusinessInformation',
    data() {
      return {
        show:true,
        // 背景图
        imageUrl:require('../../assets/callService.png'),
        QRUrl: require('../../assets/service.jpg'),
      }
    },
    methods:{
      downloadIamgeMUI(urls) {
        let that = this;
        this.mui.plusReady(()=> {
          // 创建下载任务 图片下载地址
          // var picurl = urls
          var picurl = 'http://47.52.151.96/images/2020/01/02/mtu3nzk2njuymtm0mjy4nw.jpg';
          //图片保存到手机后的路径
          var dtask = plus.downloader.createDownload(picurl, {}, function(d, status) {
            // 下载完成

            console.log(status);
            if (status == 200) {
              plus.gallery.save(d.filename, function() { //保存到相册方法
                that.$toast('图片已保存本地。')

              }, function() {
                that.$toast('图片已保存失败。')
              });
            } else {
              that.$toast('图片已保存失败。')
            }
          })
          dtask.start() //开始下载
        })

      },
      // 取消
      cancelService(){
        this.$router.push({
          name:'home'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/my";
</style>



